@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

	@media (max-width: 1300px) {
		padding-bottom: 20px;
	}

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

	  @media (max-width: 980px) {
	flex-direction: column;
		div {
			margin-bottom: 10px;
		}
  	}	


  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
