.root {
  
	li {
    display: inline-block;

    

    a {
      text-align: center;
      width: 45px;
      height: 45px;
      padding: 10px;
      border: 1px solid #fe5f1e;
      border-radius: 30px;
      margin-right: 10px;
      color: #fe5f1e;
      display: inline-block;
      cursor: pointer;

      &:hover {
        background-color: #fe5f1e;
        color: white;
      }
    }
  }
  

  :global {
	.selected {
      a {
			background-color: #fe5f1e;
      color: white;
		}
    }
  }
}
